import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { fetchUsers } from '../state/actions/userActions';
import UsersList from '../components/UsersList';

const mapStateToProps = (state) => {
  return {
    users: state.users.users,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchUsrs: () => dispatch(fetchUsers()),
  };
};

function SecondaryView (props) {
  const { users, fetchUsrs } = props;

  useEffect(() => {
    if (users === undefined) {
      fetchUsrs();
    }
  }, []);

  if (users) {
    return (
      <UsersList
        data={ users }
      ></UsersList>
    );
  } else {
    return (
      <div>Waiting</div>
    );
  }
}

SecondaryView.propTypes = {
  users: PropTypes.array,
  fetchUsrs: PropTypes.func,
};

export default connect(mapStateToProps, mapDispatchToProps)(SecondaryView);
